.modal_section {
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(15px);
  z-index: 9999;
  animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
  100% {
    opacity: 100;
  }
}

.modal_contents {
  display: flex;
  justify-content: center;
  width: 400px;
  height: 640px;
  position: relative;
}

.modal_totalview {
  position: relative;
  overflow: hidden;
  width: 100%;
  border: 3px solid #252222;
  border-radius: 15px;
}

.modal_topview {
  padding: 30px;
  background-color: #fbeddc;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal_bottomview {
  padding: 30px;
  background-color: #825b47;
  height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo_img {
  width: 200px;
  height: auto;
  margin: 30px 0px 20px 0px;
}

.logo_img2 {
  width: 300px;
  height: auto;
  margin-bottom: 10px;
}

h3 {
  font-size: 19px;
  font-family: "maple";
}

.modal_text {
  font-weight: 600;
  color: #895b49;
  text-align: center;
  font-size: 14px;
  word-break: keep-all;
  line-height: 130%;
}

.textcolor {
  color: #fbeddc;
  font-size: 18px;
}

.modal_icon {
  display: flex;
  justify-content: center;
  background-image: url("https://i.imgur.com/HbHfIjy.png");
  background-size: cover;
  width: 160px;
  height: 160px;
  position: absolute;
  top: -90px;
  z-index: 1;
  transform: rotate(-10deg);
  animation: Shake 3s infinite;
}

@keyframes Shake {
  50% {
    transform: rotate(5deg);
  }
}

.exit_button {
  position: absolute;
  bottom: -25px;
  z-index: 99;
  width: 50px;
  height: 50px;
  border: 3px solid #222;
  border-radius: 50px;
  background-color: #fbeddc;
  color: #2c2b2b;
  text-align: center;
  font-size: 20px;
}

.exit_button:hover {
  background-color: #f8c186;
  color: #fbeddc;
}

.modalPattern {
  position: absolute;
  width: 64px;
  height: 64px;
  background-image: url(https://i.imgur.com/dBuAKhc.png);
  opacity: 0.6;
  z-index: 999;
}

.mtop-left {
  top: 10px;
  left: 10px;
}

.mtop-right {
  top: 10px;
  right: 10px;
  transform: rotate(90deg);
}

.mbottom-left {
  bottom: 10px;
  left: 10px;
  transform: rotate(270deg);
}

.mbottom-right {
  bottom: 10px;
  right: 10px;
  transform: rotate(180deg);
}

